import { Component, OnInit , Inject} from '@angular/core';
import { Http } from '@angular/http';
import { WsApiService } from '../shared/ws-api.service';
import { WsService } from '../shared/ws.service';
import { Ws } from '../core/ws';

let generateRequestId = () => ('' + Math.random()).replace('0.', '');
let deviceToken = generateRequestId();
let requests = {};


@Component({
  selector: 'app-wsn',
  templateUrl: './wsn.component.html',
  styleUrls: ['./wsn.component.scss']
})
export class WsnComponent implements OnInit {

  public socket:SocketIOClient.Socket = null;

  public requests: Array<any> = [];
  public events: Array<any> = [];
  public messages: Array<any> = [];

  public wsConnectionState:boolean|string = false;
  public isTyping:boolean = false;
  public myTyping:number|string = 0;
  public otherTyping:number|string = 0;
  public badge:number|string = '';

  public API_URL: string = "http://strizhapp.ru";
  public currentUserId: number|string = 0;
  public userPhone: string = "79215820458";
  public authCode: string = "";
  public dialogId:number|string = '';
  public messageText: string = "";
  public readMessageId: number|string=0;

  public path: string = "";
  public method: string = "GET";
  public bodyParams: any = "{}";
  public queryParams: any = "{}";

  constructor(
    public wsApi: WsApiService,
    public ws: Ws,
    public http: Http,
    @Inject('APP_SETTINGS') public settings
  ) {
    window['ws'] = this.ws
    console.log('settings',this.settings )
  }

  ngOnInit() {

    setInterval(()=>{
      if (Date.now() - 4000 < this.otherTyping) {
        this.isTyping = true;
      }
      else {
        this.isTyping = false;
      }
    }, 1000);

    setInterval(() => {
      if (Date.now() - 4000 < this.myTyping) {
        this.doRequest(`api/dialog/${this.dialogId}`, 'PUT', {}, {"typing": true})
      }
    }, 3000);

    if (window.location.hash.substr(1) != '') {
      this.API_URL = window.location.hash.substr(1);
    }
  }

  onLogoutClick () {
    var reqId = generateRequestId();
    this.addRequest(reqId, {"path": "api/auth", "method": "DELETE"});
    this.http.delete(`/auth`).subscribe(
      (res:any) => {
        let data = res.json();
        this.currentUserId = data.data.user_id;
        this.addResponse(reqId, data);
      },
      (err) => console.warn('[request error]',err)
    );
  }

  onSessionCheckClick () {
    var reqId = generateRequestId();
    this.addRequest(reqId, {"path": "api/auth", "method": "GET"});

    this.http.get('/auth').subscribe(
      (res)=>{
        let data = res.json();

        this.currentUserId = data.data.user_id;

        this.addResponse(reqId, data);
        if (data.data.user_id > 0) {
          this.socketConnect();
        }
      },
      (err) => console.warn('[request error]',err)
    )
  }

  onCodeRequestClick () {
    var reqId = generateRequestId();
    this.addRequest(reqId, {"path": "api/code", "method": "POST"});
    var data = {
      "phone": this.userPhone,
      "device_type": "web",
      "device_token": deviceToken
    };

    this.http.post('/code',data).subscribe(
      (res)=>{
        let resData = res.json();
        console.log(resData);
        this.addResponse(reqId, data);
      },
      (err)=>{console.warn('[request] error',err)}
    )
  }

  onCodeAuthClick () {
    var reqId = generateRequestId();
    this.addRequest(reqId, {"path": "api/auth", "method": "POST"});
    var data = {
      "phone": this.userPhone,
      "device_type": "web",
      "device_token": deviceToken,
      "code": this.authCode,
      "type": "code",
      "system_version": "10.2.2",
      "application": "com.app",
      "application_version": "0.0.1"
    };

    this.http.post('/auth',data).subscribe(
      (res)=>{
        let data = res.json();
        this.currentUserId = data.data.user_id;
        this.addResponse(reqId, data);
        this.socketConnect();
      },
      (err)=>{console.warn('[request] error',err)}
    )
  }

  onSendRequestClick () {
    let valid = false;
    try {
      var body_params = JSON.parse(this.bodyParams);
      var query_params = JSON.parse(this.queryParams);
      valid = true;
    } catch (err) { console.warn('invalid json',err); }

    var path = this.path;
    var method = this.method;

    if (valid) this.doRequest(path, method, query_params, body_params);
  }

  onReadButtonClick () {
    var messageId = this.readMessageId;
    this.doRequest(`api/dialog/${this.dialogId}`, 'PUT', {}, {last_read_message_id:messageId});
  }

  onSendButtonClick () {
    this.doRequest('api/message', 'POST', {}, {
      "message": this.messageText,
      "dialog_id": this.dialogId
    });
    this.messageText = '';
  }

  onChatTextKeyup () {
    this.myTyping = Date.now();
  }


  socketConnect () {
    this.ws.connect();
    this.socket = this.ws.socket;

    this.socket.on('response', (data) => {
      var response = JSON.parse(data);
      console.log(response);
      this.addResponse(response.request_id, response);

      if (requests[response.request_id]) {
        var callback = requests[response.request_id];
        callback(response);
      }

    });

    this.socket.on('event', (data) => {
      console.log('[event]',data);
      this.eventProcessor(data);
      this.addEvent(data);
    });

    this.socket.on('disconnect', () => {
      console.log('disconnect');
    });

    this.wsConnectionState = 'connected';
    this.checkDialog();
  }

  addRequest (requestId, data) {
    this.requests.push({id:requestId,options:data});
  }

  addResponse (requestId, data) {
    let currentRequest = this.requests.filter(el=>el.id == requestId)[0];
    if (currentRequest)
      currentRequest['response'] = data;
  }

  addEvent (data) {

    this.events.push({event:data});
  }

  addMessage (message) {
    setTimeout(() => {
      let $messagesEl = document.getElementById('#messages');
      if ($messagesEl) {
        $messagesEl.scrollTop = $messagesEl.scrollHeight;
      }
    });

    this.messages.push(message);
  }

  doRequest (path:any, method:any, query:any, body:any, callback?:Function) {
    var requestId = generateRequestId();
    requests[requestId] = callback;
    var payloadData = {
      "path": path,
      "method": method,
      "body_params": body,
      "query_params": query,
      "request_id": requestId
    };
    if (this.ws && this.ws.subject) {
      // this.ws.request(payloadData)
      //   .then((res)=>{console.warn('wsn res',res)})
      //   .catch((err)=>{console.warn('wsn err',err)})

      this.ws.request(payloadData)
        .subscribe(
          (res)=>{
            let el = document.getElementById(res.request_id)
            if (!el) return false;
            el.style.backgroundColor = 'green';
          },
          (err)=>{console.warn('wsn err',err)}
          );


      this.addRequest(requestId, payloadData);
    }
  }

  showBadge (badge) {
    this.badge = String( badge );
  }

  checkDialog () {
    this.doRequest('api/dialog', 'GET', {}, {}, (data) => {
      this.dialogId = data.id;

      this.doRequest('api/message', 'GET', {
        filter: {dialog_id: this.dialogId},
        order: {id: 'desc'},
        page_size: 30,
        reverse:true
      },{},(data) => {

        this.messages.push(...data.data.message);
      });

    });
  }

  eventProcessor (event) {
    console.info('[this.eventProcessor]',event);
    switch (event.type) {
      case 'message':
        this.addMessage(event.data);
        if (event.data.user_id != this.currentUserId) {
          var messageId = this.readMessageId;
          console.log(messageId , event.data.id, messageId > event.data.id)
          if (messageId < event.data.id) {
            this.readMessageId = event.data.id;
          }
        }
        break;
      case 'dialog_badge':
        this.showBadge(event.data.unread_message_count);
        break;
      case 'typing':
        this.otherTyping = Date.now();
        break;
    }
  }

}



