import {ConnectionBackend, Connection, ReadyState, Request, Response, Headers, ResponseOptions} from "@angular/http";
import { Observable } from "rxjs";
import { ApiService } from "./api.service";

export class WsConnection implements Connection {
  public readyState:ReadyState;
  public request: Request;
  public response: any;
  constructor (request) {
    this.request = request;
    this.readyState = ReadyState.Open;
    this.response = new Observable((responseObserver)=>{
      let response = ApiService.buildResponse(request);
      responseObserver.next(response);
      responseObserver.complete();
    })
  }
}

export class WsConnectionBackend implements ConnectionBackend{
  createConnection(request) {
    return new WsConnection(request);
  }
}



