import { Injectable, OnInit , Inject} from '@angular/core';
import { Http,Connection, ConnectionBackend } from '@angular/http';
import { Observable, Observer,Subject } from 'rxjs';
import * as io from  "socket.io-client";

interface IWsRequest {
  path:string,
  method:string, // "GET" || "POST" ...
  body_params:any,
  query_params:{
    filter:any,
    order:{id:string}, // "desc" || "asc"
    page_size:30,
    reverse:true
  },
  request_id: string
};

interface IDeferedPromise {
  promise: Promise<any>,
  resolve: Function,
  reject: Function
};


let generateRequestId = () => ('' + Math.random()).replace('0.', '');
let deviceToken = generateRequestId();

const REQUESTS = {};

@Injectable()
export class Ws {
  static ERROR_TYPES = {
    CANCEL:'cancel'
  }

  public io: SocketIOClientStatic;
  public socket:SocketIOClient.Socket = null;
  public subject: Subject<any>;
  public REQUESTS: Object;
  public events: Array<any> = [];

  constructor(
    // public wsApi: WsApiService,
    // public ws: WsService,
    // public http: Http,
    @Inject('APP_SETTINGS') public settings
  ) {
    this.io = io;
    this.REQUESTS = REQUESTS;
  }

  get connected():boolean|string {
    return Boolean(this.socket && this.socket.connected);
  };

  get time():string {
    let d = new Date();
    return `${d.getHours()}:${d.getMinutes()}:${d.getSeconds()} ${d.getMilliseconds()}`
  }

  connect(url=this.settings.wsConnectUrl): Subject<any> {
    if(!this.subject) {
      this.subject = this.create(url);
    }

    return this.subject;
  }

  create(url): Subject<any> {
    let socket = this.socket = io.connect( this.settings.wsConnectUrl, {
      'path': '/websocket',
      'transports': ['websocket', 'polling']
    });

    let observable = Observable.create((observer: Observer<any>) => {

      socket.on('response', (data,...args) => {
        var response = JSON.parse(data);

        console.info('[WS] <- response',response);
        this.parseResponse(response,observer)
      });

      socket.on('event', (data,...args) => {
        var event = JSON.parse(data);

        console.info('[WS] <- event',event);
        this.parseEvent(event,observer)
      });

      socket.on('error', (data,...args) => {
        var error = JSON.parse(data);
        console.warn('[WS] <- error',error);

        this.parseError(error,observer)
      });

      socket.on('ping', () => {
        console.info('[WS] -> ping',this.time);
        observer.next(event);
      });
      socket.on('pong', (delay) => {
        console.info('[WS] <- pong',this.time,delay);
        observer.next(event);
      });
      socket.on('disconnect', (reason) => {
        console.log('[WS] disconnect',reason);
        observer.complete()
      });
      socket.on('connect', (...args) => {
        console.info('[WS] connected',args);
      });
      socket.on('reconnect_failed', (...args) => {
        console.info('[WS] reconnect_failed',args);
        observer.complete();
      });
      socket.on('reconnect_attempt', (...args) => {
        console.info('[WS] reconnect_attempt',args);
      });
      socket.on('reconnect_error', (...args) => {
        console.info('[WS] reconnect_error',args);
      });

      return socket.disconnect.bind(socket);
    });

    let observer = {
      next: (data: Object) => {
        if (socket) {
          console.log('[WS] -> request',data);
          socket.emit('request',data);
        }
      },
    };

    observable.subscribe(
      (res:MessageEvent)=>console.info('[WS] subscription res',res),
      err=>console.warn('[WS] subscription error',err)
    );

    return Subject.create(observer, observable);
  }

  get canSend ():boolean {
    return !!this.subject;
  }

  // request (data=this.defaultData):Promise<any> {
  request (request:IWsRequest):Observable<any> {
    request.request_id || (request.request_id = generateRequestId());

    let requestId = request.request_id;
    let requestDeferedPromise:IDeferedPromise = (()=>{
      let resolve,reject;
      let promise = new Promise((_resolve,_reject) => {
        resolve = _resolve;reject = _reject;
      });
      return {promise,resolve,reject}
    })();

    let _request = {
      request: request,
      promise: requestDeferedPromise,
      replay: ():Observable<any> => {
        return this.request(request);
      },
      cancel: () => {
        _request.canceled = true;
        return requestDeferedPromise.reject(Ws.ERROR_TYPES.CANCEL);
      },
      wasSent: false,
      hasResponse: false,
      canceled: false
    };

    if (this.canSend) {
      this.subject.next(request);
      _request.wasSent = true
    }

    REQUESTS[requestId] = _request;

    // return requestDeferedPromise.promise;
    return Observable.fromPromise(requestDeferedPromise.promise);
  }

  parseResponse (response:any,observer:Observer<any>) {
    console.info('[WS] <- response',response);
    let userId = response.user_id;
    let requestId = response.request_id;
    console.info(requestId,REQUESTS);
    if (REQUESTS[requestId]) {
      REQUESTS[requestId].promise.resolve(response);
      REQUESTS[requestId].hasResponse = true;
    }

    observer.next(response);
  }

  parseEvent (event,observer:Observer<any>) {
    console.info('[WS] <- event',event);
    observer.next(event);
  }

  parseError (error,observer:Observer<any>) {
    console.warn('[WS] <- error',error);
    observer.error(event);
  }

}



