import {Injectable} from '@angular/core';
import {Location} from '@angular/common';
import {
  ConnectionBackend, Connection, ReadyState, Request, Response, Headers, ResponseOptions, BaseRequestOptions,
  BaseResponseOptions
} from "@angular/http";
let merge = require("lodash/merge");

import { AppSettings, IAppSettings } from '../app.settings';

@Injectable()
export class ApiService {

  static settings: IAppSettings = new AppSettings();

  static buildUrl(relativeUrl: string): string {

    let urlParts = [];

    urlParts.push(
      this.settings.baseUrl,
      this.settings.apiUrl,
      relativeUrl
    );

    let resultUrl = urlParts.reduce((resultUrl,part,i)=>{
      return Location.joinWithSlash( resultUrl, part);
    },'');

    return Location.stripTrailingSlash( resultUrl );
  }

  static buildResponse (request:any):Response {
    return new Response({
      body: {
        fromApiService: {
          hello: 'world'
        }
      },
      status: 200,
      headers: new Headers(),
      url: request.url,
      merge: (options) => this.buildResponseOptions(options)
    });
  }

  static buildRequest (options:any):any {

  }

  static buildRequestOptions (options:any): any {
    return merge(BaseRequestOptions,options)
  }

  static buildResponseOptions (options:any): any {
    return merge(BaseResponseOptions,options)
  }

  static buildOptions (options:any): any {
    return options
  }

  constructor() {}

}



