import { Injectable } from '@angular/core';

export interface IAppSettings {
  debug: boolean,
  baseUrl: string,
  apiUrl: string,
  authorizationHeader: string
}

@Injectable()
export class AppSettings implements IAppSettings {

  public debug = false;
  public baseUrl = 'http://strizhapp.ru/';
  public apiUrl = '/api/';
  public wsConnectUrl = 'http://strizhapp.ru/';
  public wsApiUrl = '/api/';
  public authorizationHeader = '';

}



